main {
    min-height: 36vw;
    position: relative;
    overflow: hidden;
    z-index: 1;

    .promo {
        margin-top: 8.3vw;
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;

        &__bg {
            position: absolute;
            width: 68vw;
            height: 26vw;
            background: #FFFFFF;
            filter: blur(46px);
            z-index: 3;
        }

        &__subtitle {
            font-size: 16px;
            text-transform: uppercase;
            color: #0A0A0A;
            z-index: 5;
        }

        &__title {
            max-width: 64.6vw;
            font-family: 'Inter';
            font-weight: 700;
            font-size: 4.44vw;
            line-height: 140%;
            letter-spacing: -0.05em;
            background: linear-gradient(90deg, #0169CA 0%, #CA2DBA 50%, #9603CA 100%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            background-clip: text;
            z-index: 5;
            text-align: center;
        }

        &__buttons {
            margin-top: 32px;
            display: flex;
            flex-direction: row;
            gap: 0.55vw;
            z-index: 5;
        }
    }

    .media-icons {
        position: relative;
        z-index: 2;

        .circle {
            position: absolute;
            margin: 0 auto;
            left: 50%;
            transform: translate(-50%);
            border-radius: 100%;
            border: 1.4px solid rgba(223, 228, 234, 0.5);

            &-0 {
                width: 101vw;
                height: 101vw;
                top: -29.653vw;
                opacity: 0.3;
            }

            &-1 {
                top: -19.863vw;
                width: 81vw;
                height: 81vw;
            }

            &-2 {
                top: -10.073vw;
                width: 60vw;
                height: 60vw;
            }

            &-3 {
                top: -0.347vw;
                width: 38vw;
                height: 38vw;
            }
        }

        .icon {
            width: 4.6vw;
            height: 4.4vw;
            position: absolute;
            left: 50%;
            top: -2vw;
        }

        .linkedin {
            animation: big-r 9s linear infinite;
            transform-origin: 0% 970%;
        }

        .telegram {
            animation: big-l 8s linear infinite;
            transform-origin: 0% 970%;
        }

        .meta {
            animation: big-r 10s linear infinite;
            transform-origin: 0% 970%;
        }

        .play {
            animation: big-r 8s linear infinite;
            transform-origin: 0% 730%;
        }

        .tiktok {
            animation: big-l 9s linear infinite;
            transform-origin: 0% 730%;
        }

        .twitter {
            animation: big-r 11s linear infinite;
            transform-origin: 0% 475%;
        }
    }
}

.show-more {
    min-height: 30vw;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: height 0.5s ease, padding 0.5s ease;

    span {
        font-family: 'Inter';
        font-weight: 300;
        font-size: 2.22vw;
        line-height: 140%;
        text-align: center;
        letter-spacing: -0.05em;
        color: #9CA3AF;
        transition: color 0.3s ease-in-out;
        z-index: 100;
    }
}

@media (max-width: 575px) {
    main {
        .promo {
            &__subtitle {
                font-size: 12px;
            }

            &__title {
                font-size: 8.5vw;
                max-width: 100vw;
            }

            &__buttons {
                margin-bottom: 24px;
            }
        }
    }

    .show-more {
        padding: 24px;

        span {
            font-size: 4vw;

        }
    }
}


@keyframes big-l {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

@keyframes big-r {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(-360deg);
    }
}

@keyframes unrotate {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(-360deg);
    }
}