.footer {
    background: #FCFCFD;

    .container {
        width: 100%;
        display: flex;
        justify-content: center;

    }

    &__text {
        margin: 0 16px;
        min-height: 25.1vw;
        display: flex;
        justify-content: center;
        align-items: center;
        font-weight: 300;
        font-size: 32px;
        line-height: 140%;
        text-align: center;
        color: #23262F;
    }

    footer {
        background: #F4F4F4;
        padding: 2.2vw;
    }

    &__wrapper {
        min-height: 24px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 1180px;

        .logo {
            display: flex;
            align-items: center;
        }

        img {
            width: 32px;
            height: 32px;
            margin-right: 0.55vw;
        }

        h2 {
            font-size: 16px;
            letter-spacing: -0.03em;
            text-transform: uppercase;
            background: linear-gradient(90deg, #0169CA 0%, #CA2DBA 50%, #9603CA 100%), #1E61C8;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            background-clip: text;
        }
    }

    .nav {
        ul {
            list-style: none;
            display: flex;
            align-items: center;
            gap: 16px;
        }

        a {
            text-decoration: none;
            letter-spacing: -0.03em;
        }

        &__link {
            a {
                color: #4B5563;
                font-size: 16px;
                font-weight: 400;
                letter-spacing: -0.03em;
            }
        }

    }
}

@media (max-width: 575px) {
    .footer {
        .container {
            max-width: 100%;
        }

        &__wrapper {
            display: flex;
            flex-direction: column;
            gap: 16px;
        }

        &__text {
            min-height: 30vw;
            font-size: 16px;
        }
    }
}