.contact {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    background: #FAFAFA;
    border-radius: 40px;
    padding: 2.7vw 4vw;
    margin-bottom: 60px;

    &__form {
        &_title {
            font-weight: 400;
            font-size: 24px;
            line-height: 140%;
            color: #000000;
            margin-bottom: 8px;
        }

        &_descr {
            font-weight: 400;
            font-size: 18px;
            line-height: 140%;
            color: #6B7280;
            margin-bottom: 32px;
        }

        form {
            display: flex;
            flex-direction: row;

            input {
                width: 300px;
                height: 46px;
                background: #F3F4F6;
                border-radius: 12px;
                font-weight: 400;
                font-size: 16px;
                line-height: 140%;
                color: #6B7280;
                border: none;
                margin-right: 8px;
                padding-left: 16px;
            }
        }

        &_btn {
            width: 87px;
            height: 46px;
        }
    }

    &__img {
        img {
            max-height: 26.8vw;
        }
    }

}

.popup {
    position: fixed;
    z-index: 10;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.4);
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 16px;
    opacity: 0;
    animation: fadeIn 1s forwards;

    &__content {
        background: #FFFFFF;
        border-radius: 10px;
        padding: 16px;
        display: flex;
        flex-direction: column;
        position: relative;
        transform: translateY(-50px);
        animation: slideIn 1s forwards;
    }

    &__wrapper {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        gap: 20px;
        height: 100%;
        width: 100%;
    }

    &__title {
        margin-top: 3vw;
        font-weight: 700;
        font-size: 18px;
        text-align: center;
        color: #34383C;

    }

    &__subtitle {
        font-weight: 500;
        font-size: 16px;
        text-align: center;
        color: #78838B;
        max-width: 60%;
    }

    &__btn {

        width: 80%;
        margin-bottom: 1.5vw;
    }

}

.close {
    position: absolute;
    width: 16px;
    height: 16px;
    cursor: pointer;

    &::before,
    &::after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        width: 100%;
        height: 2px;
        background-color: #94A2AC;


    }

    &::before {
        transform: translate(-50%, -50%) rotate(45deg);
    }

    &::after {
        transform: translate(-50%, -50%) rotate(-45deg);
    }

}

.no-scroll {
    overflow: hidden;
}

@keyframes fadeIn {
    to {
        opacity: 1;
    }
}

@keyframes slideIn {
    to {
        transform: translateY(0);
    }
}

@media (max-width: 767px) {
    .contact__form form input {
        width: 230px;
    }
}

@media (max-width: 575px) {
    .contact {
        background: none;

        &__img {
            display: none;
        }

        &__form {
            &_title {
                text-align: center;
            }

            &_descr {
                font-size: 14px;
                text-align: center;
            }


            form {
                justify-content: center;

                input {
                    width: 100%;
                }
            }

        }
    }
}