.about-us {
    padding: 4.4vw 0;
    display: flex;
    flex-direction: column;
    gap: 8.3vw;

    &__item {
        display: flex;
        flex-direction: row;
        gap: 9vw;
        align-items: center;
        justify-content: center;
        padding: 0 3.4vw;
    }

    &__title {
        font-weight: 700;
        font-size: 3.3vw;
        line-height: 140%;
        background: linear-gradient(90deg, #0169CA 0%, #CA2DBA 50%, #9603CA 100%), #000000;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        margin-bottom: 1.6vw;
    }

    &__descr {
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 140%;
        color: #6B7280;
    }

    &__img {
        img {
            max-width: 27.8vw;
        }
    }
}

@media (max-width: 575px) {
    .about-us {


        &__descr {
            font-size: 12px;

        }
    }
}