@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&family=Onest:wght@100..900&display=swap');

body,
* {
    margin: 0;
    padding: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-family: "Onest", sans-serif;
}

.container {
    max-width: 82%;
    margin: 0 auto;
}

.btn {
    width: 118px;
    height: 36px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    border-radius: 12px;
    background: none;
    cursor: pointer;



    a {
        text-decoration: none;
        font-weight: 500;
        font-size: 0.9vw;
    }

    &__contact {
        background: #1F61C8;
        color: #FFFFFF;

        a {
            color: #FFFFFF;
        }
    }
}

li {
    display: flex;
    justify-content: center;
    align-items: center;
}

@media (max-width: 991px) {
    .container {
        max-width: 93%;
    }
}