.choose {
    padding: 8.3vw 0;
    background: #F5F5F5;

    &__title {
        font-weight: 400;
        font-size: 3.3vw;
        line-height: 140%;
        color: #000000;
        margin-bottom: 32px;
    }

    &__wrapper {
        display: grid;
        grid-template-columns: auto auto;
        grid-template-rows: auto auto;
        gap: 2.2vw;
    }

    &__item {
        padding: 32px 32px 49px;
        background: #FAFAFA;
        border-radius: 24px;

        img {
            width: 48px;
            height: 48px;
            background: #E5E5E5;
            border-radius: 8px;
            margin-bottom: 32px;
        }

        &_title {
            font-weight: 400;
            font-size: 24px;
            line-height: 140%;
            color: #23262F;
            margin-bottom: 16px;
        }

        &_descr {
            font-weight: 400;
            font-size: 12px;
            line-height: 140%;
            color: #777E91;

        }


    }

}

@media (max-width: 767px) {
    .choose {


        &__title {
            font-size: 4.1vw;
        }
    }
}

@media (max-width: 575px) {
    .choose {
        &__title {
            font-size: 7.4vw;
        }

        &__wrapper {
            grid-template-columns: auto;
            gap: 16px;
        }
    }
}