.header {
    min-height: 5.83vw;
    overflow: hidden;
    margin-top: 1.1vw;
    display: flex;
    justify-content: center;


    header {
        display: flex;
        justify-content: space-between;
        flex-direction: row;
        align-items: center;
        background: rgba(242, 242, 242, 0.7);
        backdrop-filter: blur(4);
        border-radius: 16px;
        height: 100%;

        min-width: 74vw;
        padding: 10px 16px;


        .logo {
            display: flex;
            flex-direction: row;
            align-items: center;

            img {
                height: 32px;
                width: 32px;
                margin-right: 0.5vw;
            }

            &__title {
                text-transform: uppercase;
                font-weight: 400;
                font-size: 16px;
                line-height: 100%;
                letter-spacing: -0.03em;
                background: linear-gradient(90deg, #0169CA 0%, #CA2DBA 50%, #9603CA 100%), #1E61C8;
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
                background-clip: text;
            }
        }

        .nav {

            ul {
                list-style: none;
                display: flex;
                align-items: center;
                gap: 1.11vw;


            }


            a {
                text-decoration: none;
                letter-spacing: -0.03em;
            }

            &__link {
                a {
                    color: #4B5563;
                    font-size: 16px;
                    font-weight: 400;
                }
            }
        }
    }
}

@media (max-width: 991px) {
    .header {
        header {
            min-width: 90vw;
        }
    }
}

@media (max-width: 575px) {
    .header .nav__link {
        display: none;
    }
}